import { Card } from '@/types/statistics';
import EmployeesList from '../components/employeesList/EmployeesList';
import EmployeesStatistics from '../components/statistics/EmployeesStatistics';
import UserRemoveIcon from '@/components/icons/user/UserRemove';
import TwoUsersIcon from '@/components/icons/user/TwoUsersIcon';
import MinusUserIcon from '@/components/icons/user/MinusUserIcon';
import GroupIcon from '@/components/icons/user/GroupIcon';
import { useEffect, useState } from 'react';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { employeesStats } from '../types/employeesStats.types';

export default function EmployeesPage() {
    const [stats, setStats] = useState<employeesStats | null>(null);

    useEffect(() => {
        const fetchStats = async () => {
            try {
                const res = await apiGet({ endpoint: endpoints.employee.dashboard });
                setStats(res.data.data);
            } catch (err) {
                setStats(null);
            }
        };
        fetchStats();
    }, []);

    const cards: Card[] = [
        { text: 'إجمالي العاملين', num: stats?.total_employees, percent: 12, icon: <GroupIcon /> },
        { text: 'متوسط التقيم', num: 35, percent: 0, icon: <UserRemoveIcon /> },
        { text: 'العاملين دوام كلي', num: stats?.total_full_time, percent: -0.3, icon: <TwoUsersIcon /> },
        { text: 'العاملين دوام جزئي', num: stats?.total_part_time, percent: -0.3, icon: <MinusUserIcon /> },
    ];

    return (
        <div className="w-full mt-6 mb-20">
            <EmployeesStatistics cards={cards} />
            <EmployeesList />
        </div>
    );
}
